
import * as THREE from 'three';
import * as CANNON from 'cannon-es';


export class Land{

  private planeBody:CANNON.Body;
  private planeMesh:THREE.Mesh;
    
  constructor(landId:string, x:number, y:number, material:CANNON.Material, onEnter:Function){
    //Physics-CANNON
    const landShape = new CANNON.Box(new CANNON.Vec3(4, 4, 0.1));
    this.planeBody = new CANNON.Body({ mass: 0, material: material  })
    this.planeBody.position.x=x;
    this.planeBody.position.z=y;
    this.planeBody.addShape(landShape)
    this.planeBody.quaternion.setFromAxisAngle(new CANNON.Vec3(1, 0, 0), -Math.PI / 2)

    this.planeBody.addEventListener("collide", (event:any)=>{ 
        onEnter(landId);
    })

    //Render-THREE
    const phongMaterial = new THREE.MeshPhongMaterial()
    phongMaterial.color = new THREE.Color( Math.random() * 0xffffff );
    const planeGeometry = new THREE.PlaneGeometry(8, 8)
    this.planeMesh = new THREE.Mesh(planeGeometry, phongMaterial)
    this.planeMesh.rotateX(-Math.PI / 2)
    this.planeMesh.receiveShadow = true
    this.planeMesh.position.x = x;
    this.planeMesh.position.z = y;
  }

  addTo(world:CANNON.World, scene:THREE.Scene){
    world.addBody(this.planeBody);
    scene.add(this.planeMesh);
  }
}