
import * as THREE from 'three';
import * as CANNON from 'cannon-es';

import Stats from 'three/examples/jsm/libs/stats.module.js';
import CannonDebugRenderer from './utils/cannonDebugRenderer'
import {Ground} from './Ground'
import {Land} from './Land'
import {Player} from './Player'


export class KhoLand{

  private keys:any = {
    a: false,
    s: false,
    d: false,
    w: false,
    e: false
  };

  private scene: THREE.Scene;
  private camera: THREE.PerspectiveCamera;
  private renderer: THREE.WebGLRenderer;
  private world: CANNON.World;
  private stats:Stats;
  private clock = new THREE.Clock()
  private delta=0;
  private cannonDebugRenderer: CannonDebugRenderer;
  private player:Player;
  //private ground:Ground;
  private onLandActivate:Function;

  constructor(onLandActivate:Function){
    this.onLandActivate=onLandActivate;
  }

  init=(root:any)=>{
    this.world = new CANNON.World();
    this.world.gravity.set(0, -9.82, 0)
    // world.broadphase = new CANNON.NaiveBroadphase()
    // ;(world.solver as CANNON.GSSolver).iterations = 10
    // world.allowSleep = true

    console.log(root.clientWidth);
    console.log(root.clientHeight);

    const width = root.clientWidth; // window.innerWidth
    const height = window.innerHeight;

    this.scene = new THREE.Scene();
    this.scene.add(new THREE.AxesHelper(5))
    this.camera = new THREE.PerspectiveCamera( 30, width / height, 0.1, 1000 );
    this.camera.position.set( 0, 4, -10 );
    const lookAt = this.scene.position.clone();
    lookAt.z = 10;
    this.camera.lookAt(lookAt);

    this.renderer = new THREE.WebGLRenderer({antialias:true, alpha: true})
    this.renderer.setClearColor( 0xffffff, 0 );
    this.renderer.setSize(width, height)
    this.renderer.shadowMap.enabled = true
    root.append(this.renderer.domElement)

    // Set materials for friction
    const groundMaterial = new CANNON.Material('ground');
    // Create a slippery material (friction coefficient = 0.0)
    const slipperyMaterial = new CANNON.Material('slippery');
    // The ContactMaterial defines what happens when two materials meet.
    // In this case we want friction coefficient = 0.0 when the slippery material touches ground.
    const slipperyGround = new CANNON.ContactMaterial(groundMaterial, slipperyMaterial, {
      friction: 0,
      restitution: 0.3,
      contactEquationStiffness: 1e8,
      contactEquationRelaxation: 3,
    })

    // We must add the contact materials to the world
    this.world.addContactMaterial(slipperyGround)

    // Adjust constraint equation parameters for ground/ground contact
    const groundGround = new CANNON.ContactMaterial(groundMaterial, groundMaterial, {
      friction: 0.2,
      restitution: 0.3,
      contactEquationStiffness: 1e8,
      contactEquationRelaxation: 3,
      frictionEquationStiffness: 1e8,
    })

    // Add contact material to the world
    this.world.addContactMaterial(groundGround)
    
    this.addLights();
    this.addEvents();
    this.addDebug();

    for(let i=-1; i<2;i++){
      for(let j=-1; j<2;j++){
        if(i===0 && j===0){
          continue;
        }
        const size=10;
        const index=(i+1)*3 + (j+1)
        const land = new Land(`land-${index}`, i*size, j*size, groundMaterial, this.onLandEnterFn);
        land.addTo(this.world, this.scene);
      }
    }
    const ground = new Ground(groundMaterial);
    ground.addTo(this.world, this.scene);

    this.player = new Player(this.camera, slipperyMaterial);
    this.player.addTo(this.world, this.scene);
  }

  private currentActiveLand:string = null;

  onLandEnterFn=(landId:string)=>{
    if(this.currentActiveLand!==landId){
      this.currentActiveLand = landId;
      console.log(`Enter on land: ${landId}`); 
    }
  }

  addLights=()=>{
    const light = new THREE.AmbientLight( 0xffffff ); // soft white light
    this.scene.add( light );

    const dirLight = new THREE.DirectionalLight( 0xffffff );
    dirLight.position.set( 0, 200, 100 );
    dirLight.castShadow = true;
    dirLight.shadow.camera.top = 180;
    dirLight.shadow.camera.bottom = - 100;
    dirLight.shadow.camera.left = - 120;
    dirLight.shadow.camera.right = 120;
    this.scene.add( dirLight );
  }

  addEvents=()=>{
    const scope = this;

    const keyPress=(key)=>{
      scope.keys[key] = true;
      setTimeout(()=>{
        scope.keys[key] = false;
      }, 500)
    }

    (window as any).move = (x:number,y:number) => {
      if(x<0){
        keyPress('a')
      }else if(x>0){
        keyPress('d')
      }
      if(y<0){
        keyPress('s')
      }else if(y>0){
        keyPress('w')
      }
    }

    window.addEventListener('resize', ()=>{
      scope.camera.aspect = window.innerWidth / window.innerHeight
      scope.camera.updateProjectionMatrix()
      scope.renderer.setSize(window.innerWidth, window.innerHeight)
      scope.render()
    }, false)

    document.body.addEventListener( 'keydown', (event:KeyboardEvent)=>{
      const key = event.code.replace('Key', '').toLowerCase();
      if ( scope.keys[ key ] !== undefined )
        scope.keys[ key ] = true;
    });
    document.body.addEventListener( 'keyup', (event:KeyboardEvent)=>{
      const key = event.code.replace('Key', '').toLowerCase();
      if ( scope.keys[ key ] !== undefined )
        scope.keys[ key ] = false;
    });
  }

  addDebug=()=>{
    this.stats = Stats()
    document.body.appendChild(this.stats.dom)
    this.cannonDebugRenderer = new CannonDebugRenderer(this.scene, this.world);
  }

  private angle = 0;

  animate=()=> {
    //requestAnimationFrame(this.animate)

    this.delta = Math.min(this.clock.getDelta(), 0.1)
    this.world.step(this.delta)

    if ( this.keys.e ){
      this.onLandActivate(this.currentActiveLand);
    }

    let speed = 0.0;
    if ( this.keys.w )
      speed = 4;
    else if ( this.keys.s )
      speed = -4;

    if ( this.keys.a )
      this.angle+=0.02;
    else if ( this.keys.d )
      this.angle-=0.02;
    
    this.angle = this.angle % (2*Math.PI)
    this.player.turn(this.angle);
    this.player.move(speed);
    this.player.animate();

    this.stats.update()
    this.cannonDebugRenderer.update()

    this.render()
  }

  render=()=> {
    this.renderer.render(this.scene, this.camera)
  }

}

