
import * as THREE from 'three';
import * as CANNON from 'cannon-es';


export class Ground{

  private planeBody:CANNON.Body;
  private planeMesh:THREE.Mesh;

  constructor(material:CANNON.Material){
    //Physics-CANNON
    const planeShape = new CANNON.Plane()
    this.planeBody = new CANNON.Body({ mass: 0, material: material  })
    this.planeBody.addShape(planeShape)
    this.planeBody.quaternion.setFromAxisAngle(new CANNON.Vec3(1, 0, 0), -Math.PI / 2)

    //Render-THREE
    const phongMaterial = new THREE.MeshPhongMaterial()
    const planeGeometry = new THREE.PlaneGeometry(100, 100)
    this.planeMesh = new THREE.Mesh(planeGeometry, phongMaterial)
    this.planeMesh.position.y = -0.01
    this.planeMesh.rotateX(-Math.PI / 2)
    this.planeMesh.receiveShadow = true
  }

  addTo(world:CANNON.World, scene:THREE.Scene){
    world.addBody(this.planeBody);
    scene.add(this.planeMesh);
  }
}