
import * as THREE from 'three';
import * as CANNON from 'cannon-es';
import {FBXLoader} from 'three/examples/jsm/loaders/FBXLoader.js';

export class Player{

    private cubeBody:CANNON.Body;
    private cubeMesh:THREE.Object3D;
    private camera:THREE.Camera;
    private mixer:THREE.AnimationMixer;
    private clock = new THREE.Clock();
    constructor(camera: THREE.PerspectiveCamera, material: CANNON.Material){
      this.camera = camera;
      this.cubeBody = new CANNON.Body({ mass: 1, material: material });
    }

    calculateBoxSize(object: THREE.Object3D){
      let minX = Number.MAX_SAFE_INTEGER;
      let minY = Number.MAX_SAFE_INTEGER;
      let minZ = Number.MAX_SAFE_INTEGER;
      let maxX = Number.MIN_SAFE_INTEGER;
      let maxY = Number.MIN_SAFE_INTEGER;
      let maxZ = Number.MIN_SAFE_INTEGER;

      if (object instanceof THREE.Object3D) {
        object.traverse ((mesh)=>{
          if (mesh instanceof THREE.Mesh) {            
						mesh.castShadow = true;
						mesh.receiveShadow = true;

            mesh.geometry.computeBoundingBox ();
            const bBox = mesh.geometry.boundingBox;
            // compute overall bbox
            minX = Math.min (minX, bBox.min.x);
            minY = Math.min (minY, bBox.min.y);
            minZ = Math.min (minZ, bBox.min.z);
            maxX = Math.max (maxX, bBox.max.x);
            maxY = Math.max (maxY, bBox.max.y);
            maxZ = Math.max (maxZ, bBox.max.z);
          }
        });
        const bBoxX = (maxX-minX)/2;  
        const bBoxY = (maxY-minY)/2;
        const bBoxZ = (maxZ-minZ)/2;
        return new CANNON.Vec3(bBoxX, bBoxY, bBoxZ);
      }
      return new CANNON.Vec3(0, 0, 0);
    }
  
    addTo(world:CANNON.World, scene:THREE.Scene){
      const fbxLoader = new FBXLoader();
      const scope = this;
      fbxLoader.load( 'models/Girl2.fbx', function ( object ) {
        scope.cubeMesh = object;
        scope.cubeMesh.add(scope.camera);

        const cubeShape = new CANNON.Box(scope.calculateBoxSize(scope.cubeMesh))
        scope.cubeBody.addShape(cubeShape)
        scope.cubeBody.position.y = 1

        world.addBody(scope.cubeBody);
        scene.add (scope.cubeMesh);
        scope.mixer = new THREE.AnimationMixer(scope.cubeMesh);
        fbxLoader.load('models/Silly-Dancing.fbx', (walkAnimation)=>{
          console.log(walkAnimation.animations[0]);
          const animationAction = scope.mixer.clipAction(walkAnimation.animations[0])
          //animationAction.play();
          //animationAction.stop();
        });
      });
    }
  
    mesh(){
      return this.cubeMesh;
    }
  
    body(){
      return this.cubeBody;
    }
  
    animate(){
      // Copy coordinates from Cannon to Three.js
      this.cubeMesh && this.cubeMesh.position.set(
        this.cubeBody.position.x,
        // TODO: fixed following calucation, meanwhile use 0. 
        // this.cubeBody.position.y,
        0,
        this.cubeBody.position.z
      )
      this.cubeMesh && this.cubeMesh.quaternion.set(
        this.cubeBody.quaternion.x,
        this.cubeBody.quaternion.y,
        this.cubeBody.quaternion.z,
        this.cubeBody.quaternion.w
      )

      this.mixer && this.mixer.update(this.clock.getDelta())
    }
  
    turn(angle:number){
      // TODO: use lerp to make smooth follow
      const axis = new CANNON.Vec3(0,1,0);
      this.cubeBody.quaternion.setFromAxisAngle(axis, angle);
    }
  
    move(speed:number){
      // TODO: use lerp to make smooth follow
      const localForward = new CANNON.Vec3(0,0,1); // local front direction of body
      const worldForward = new CANNON.Vec3();
      this.cubeBody.vectorToWorldFrame(localForward, worldForward);
  
      const newVelocity = worldForward.scale(speed).clone();
      newVelocity.y = this.cubeBody.velocity.y;
      this.cubeBody.velocity.copy(newVelocity);
    }
  }