
import React, { useRef, useEffect } from 'react'
import {KhoLand} from '../Kholand/KhoLand';


import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

const Canvas = props => {
  
  const onLandEnter = (landId) =>{
    if(activeLand!==landId){
      console.log("Canvas.onLandEnter", landId);
      setActiveLand(landId);
      handleOpen();
    }
  }

  const khoLand = new KhoLand(onLandEnter);
  
  const divRef = useRef(null)

  const [open, setOpen] = React.useState(false);
  const [activeLand, setActiveLand] = React.useState("");
  const handleOpen = () => setOpen(true);
  const handleClose = () => {
    setActiveLand("");
    setOpen(false);
  }
  
  useEffect(() => {  
    const div = divRef.current
    khoLand.init(div);

    let animationFrameId

    //Our draw came here
    const render = () => {
      khoLand.animate();
      animationFrameId = window.requestAnimationFrame(render)
    }
    render()

    return () => {
      window.cancelAnimationFrame(animationFrameId)
    }
  }, [khoLand])
  
  return (
  <> 
    <Modal
  open={open}
  onClose={handleClose}
  aria-labelledby="modal-modal-title"
  aria-describedby="modal-modal-description"
>
  <Box sx={style}>
    <Typography id="modal-modal-title" variant="h6" component="h2">
      Activate land [{activeLand}]
    </Typography>
    <Typography id="modal-modal-description" sx={{ mt: 2 }}>
      Put game frame here.
    </Typography>
  </Box>
</Modal>
  
    <div ref={divRef} {...props}/>
    </>
    
    )
}

export default Canvas